<template>
    <div class="radios">
      <table>
        <td class="SortBy">
          Sort By:
        </td>
        <td>

          <table class="radioAll">
            <td>
              <button class="radio1" 
              v-bind:class="{ StatusRadioActiv: radioStatus == 1, StatusRadioNoActiv: !radioStatus != 2 }"
              @click="SortByData"
              >Release date</button>
            </td>
            <td>
              <button class="radio2" 
              v-bind:class="{ StatusRadioActiv: radioStatus == 2, StatusRadioNoActiv: radioStatus != 2 }"
              @click="SortByNs"
              >NS-Servers</button>
            </td>
            <td>
              <button class="radio3" 
              v-bind:class="{ StatusRadioActiv: radioStatus == 3, StatusRadioNoActiv: !radioStatus != 2 }"
              @click="SortByReg"
              >Registrant</button>
            </td>
          </table>

        </td>
      </table>

      <div class="AnalizMain">
        <table v-if="radioStatus == 1" class="TableAnaliz">
          <tr>
            <td>Domain name</td>
            <td>Age</td>
            <td>Release date</td>
            <td>NS-Servers</td>
            <td>Registrant</td>
          </tr>
          <tr v-for="(domainA,key) in domainsAnaliz10" :key="key">
              <td class="domenNameAnaliz" @click="$emit('pushDomain', domainA.DomainName)">{{ domainA.DomainName }}</td>
              <td>{{ domainA.Age }}</td>
              <td>{{ domainA.ReleaseDate.split('T')[0] }}</td>
              <td>
                  <p v-for="(nsServer,key) in domainA.NsServers.split(' ')" :key="key"> {{ nsServer }} </p>
              </td>
              <td>{{ domainA.Registrant }}</td>
          </tr>
        </table>

        <table v-if="radioStatus == 2" class="TableAnaliz">
          <tr>
            <td>NS-Servers</td>
            <td>Count</td>
          </tr>
          <tr v-for="(domainA,key) in nsServersAnaliz" :key="key">
            <td>{{ domainA[0] }}</td>
            <td>{{ domainA[1] }}</td>
          </tr>
        </table>

        <table v-if="radioStatus == 3" class="TableAnaliz">
          <tr>
            <td>Registrant</td>
            <td>Count</td>
          </tr>
          <tr v-for="(domainA,key) in registrantAnaliz" :key="key">
            <td>{{ domainA.Registrant }}</td>
            <td>{{ domainA.reg_count }}</td>
          </tr>
        </table>

        <table v-if="radioStatus == 1" class="NavigationAnaliz">
          <td @click="GoPage(-2)"> &lt;&lt; </td>
          <td @click="GoPage(-1)"> &lt; </td>
          <td>{{ PageDomainInAnaliz }}</td>
          <td @click="GoPage(1)"> &gt; </td>
          <td @click="GoPage(2)"> &gt;&gt; </td>
        </table>
      </div>

    </div>
</template>

<script>
import WhoisDataService from '../services/WhoisDataService';

export default {
  name: 'AnalizDomains',
  data() {
    return {
      radioStatus: 1,
      PageDomainInAnaliz: 1,
      CountPage: 0,
      domainsAnaliz10: [],
      nsServersAnaliz: [],
      registrantAnaliz: [],
    };
  },
  created (){
    this.SortByData();
    WhoisDataService.GetCountDomain(this.PageDomainInAnaliz)
          .then(response => {
            this.CountPage = Math.ceil(response.data[0].reg_count / 10);
      });
  },
  methods: {
    GoPage(direction){
      if(direction == 1 && this.PageDomainInAnaliz < this.CountPage) this.PageDomainInAnaliz += 1;
      else if (direction == -1 && this.PageDomainInAnaliz > 1) this.PageDomainInAnaliz -= 1;
      else if (direction == -2) this.PageDomainInAnaliz = 1;
      else if (direction == 2) this.PageDomainInAnaliz = this.CountPage;
      this.SortByData();
    },
    SortByData() {
      this.radioStatus = 1;
      WhoisDataService.get10(this.PageDomainInAnaliz)
          .then(response => {
            this.domainsAnaliz10 = response.data;
            // console.log(response);
      });
    },
    SortByNs(){
      this.radioStatus = 2;
      WhoisDataService.GetNsServers()
          .then(response => {
            var nsArr = new Map();
            var nsServersArr = response.data;
            nsServersArr.forEach(el => {
              el.NsServers.split(' ').forEach(nss => {
                if(!nsArr.has(nss))
                  nsArr.set(nss, el.ns_count);
                else 
                  nsArr.set(nss, nsArr.get(nss) + el.ns_count);
              })
            });
            this.nsServersAnaliz = new Map([...nsArr.entries()].sort((a, b) => b[1] - a[1]));
            // console.log(this.nsServersAnaliz);
      });
    },
    SortByReg(){
      this.radioStatus = 3;
      WhoisDataService.GetRegistrant()
          .then(response => {
            this.registrantAnaliz = response.data;
            // console.log(response);
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.NavigationAnaliz{
  margin: 1vw 2vw;
  width: 95%;
  border-collapse: collapse;
  text-align: center;
  font-size: calc(0.5em + 1vw);
}

.NavigationAnaliz td{
  width: 2vw;
}

.NavigationAnaliz td:hover{
  opacity: 0.2;
}

.TableAnaliz{
  margin: 0 2vw;
  width: 95%;
  border-collapse: collapse;
  font-size: calc(0.5em + 1vw);
}

.TableAnaliz td {
  border-bottom: 1px solid black; /* Линия снизу */
  padding: 9px 8px;
  transition: .3s linear;
}

.TableAnaliz p{
  margin: 0;
}

.AnalizMain{
  opacity: 0.9;
  margin-top: 2vw;
  border: 2px solid #000;
  border-radius: 30px;
  background: #AAA;
  width: 90vw;
}

.domenNameAnaliz:hover {
  opacity: 0.3;
}

.SortBy{  
  font-size: calc(0.9em + 1vw);
  color: #DDD;
  text-shadow: #000 10px 5px 10px;
  padding-right: 0.5vw;
}

.radios{
  margin-top: 2vh;
}

.radioAll{
  background: #AAA;
  border-radius: 30px;
  opacity: 0.8;
  border: 2px solid #000;
}

.StatusRadioNoActiv {
    background: #222;
    color: #DDD;
}

.StatusRadioActiv{
  background: #AAA;
  color: #222;
}

.radioAll button{
  font-size: calc(0.2em + 1vw);
  border: none;
  padding: 5px;
}

.radio1 {
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  margin-left: 2px;
}
.radio3{
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  margin-right: 2px;  
}

</style>