import http from "../http-common";
class WhoisDataService {
  FindByEmail(email) {
    return http.get(`/whoisdbs/users/${email}`);
  }
  create(data) {
    return http.post("/whoisdbs/users/1", data);
  }
  update(email, data) {
    return http.put(`/whoisdbs/users/${email}`, data);
  }
  GetPassword(email, data) {
    return http.post(`/whoisdbs/users/log/${email}`, data);
  }
  delete(email) {
    return http.delete(`/whoisdbs/users/${email}`);
  }
  getWhoisInfo(data) {
    return http.post(`/whoisdbs/getdomain`, data);
  }
  get10(id){
    return http.get(`/whoisdbs/${id}`);
  }
  GetNsServers(){
    return http.get(`/whoisdbs/nsservers/1`);
  }
  GetRegistrant(){
    return http.get(`/whoisdbs/registrant/1`);
  }
  GetCountDomain(){
    return http.get(`/whoisdbs/GetCountDomain/1`);
  }
  AddDomain(domain, data){
    return http.post(`/whoisdbs/users/Domain/${domain}`, data);
  }
  DeleteDomain(domain, data){
    return http.put(`/whoisdbs/users/Domain/${domain}`, data);
  }
  GetDomain(data){
    return http.post(`/whoisdbs/users/Domains/1`, data);
  }
}
export default new WhoisDataService();