<template>
  <div v-if="width > 330" class="Main">
    <p v-if="width > 1100" class="Title">WHOISCHECKER</p>

    <table v-else>
      <tr>
        <p class="Title">WHOIS</p>
      </tr>
      <tr>
        <p class="Title">CHECKER</p>
      </tr>
    </table>
    <div class="btnMain">
      
      <table>
        <tr>
          <td colspan="2">
            <button class="GoToAnalizBtn" v-if="all.info.length > 0" @click="all.info = []">Go to analysis</button>
          </td>
        </tr>
        <tr>
          <td>
            <UserDomains :userEmail="userEmail" v-if="userStatus" class="btnMainbtn" @pushDomain="(domainName) => this.domainName = domainName"/>
          </td>
          <td>
            <button class="btnMainbtn" @click="authForm = !authForm">LogIn</button>
          </td>
        </tr>
        
      </table>

    </div>
  </div>

  <div v-else>
    <div class="MainMiniEmpty"></div>
    <div class="MainMini">
      <table class="MainMiniTable">
        <tr>
          <td>
            <UserDomains :userEmail="userEmail" v-if="userStatus" @pushDomain="(domainName) => this.domainName = domainName"/>
          </td>
          <td>
            <button @click="authForm = !authForm">LogIn</button>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <button class="GoToAnBTN" v-if="all.info.length > 0" @click="all.info = []">Go to analysis</button>
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <p>WHOISCHECKER</p>
          </td>
        </tr>
      </table>
    </div>
  </div>

  <div class="InfoMain">
    <div class="inputBlock">
        <input type="text"
            placeholder="write domen`s name"
            v-model="domainName">
        <button class="SearchBtn" type="submit" v-on:click="GetHoisInfo">
            <img src="./components/images/loup.png" width="30" style="padding-top: 10px;">
        </button>
        <div v-if="all.info.length > 0">
            <button v-if="userStatus" class="btnAddDomen" @click="trackDomen">Track domen</button>
            <h1>{{domenNameTemp}}</h1>
            <BlockWithInfo :info="status.info" :typeName="status.type" />
            <BlockWithInfo :info="date.info" :typeName="date.type" :pathImg="date.path" />
            <BlockWithInfo :info="registrant.info" :typeName="registrant.type" :pathImg="registrant.path" />
            <BlockWithInfo :info="admin.info" :typeName="admin.type" :pathImg="admin.path" />
            <BlockWithInfo :info="tech.info" :typeName="tech.type" :pathImg="tech.path" />
            <BlockWithInfo :info="all.info" :typeName="all.type" />
        </div>
    </div>
    <AnalizDomains v-if="all.info.length == 0" @pushDomain="(domainName) => this.domainName = domainName"/>
  </div>

  <AuthForm v-show="authForm" @authorizationClick="(hideForm, uemail, ustatus) => StatusUserCheck(hideForm, uemail, ustatus)"/>
</template>

<script>
import AuthForm from './components/AuthForm.vue';
import UserDomains from './components/UserDomains.vue';
import BlockWithInfo from './components/BlockWithInfo.vue';
import AnalizDomains from './components/AnalizDomains.vue';
import WhoisDataService from './services/WhoisDataService';

export default {
  name: 'App',
  components: {
    AuthForm,
    UserDomains,
    BlockWithInfo,
    AnalizDomains
},
  data() {
    return {
        width: window.innerWidth,
        authForm: false,
        domainName: "",
        domenNameTemp: "", 
        status: { info: [], type: 'Status', path: '' },
        all: { info: [], type: 'All', path: '' },
        date: { info: [], type: 'Date', path: 'date' },
        registrant: { info: [], type: 'Registrant', path: 'person' },
        admin: { info: [], type: 'Admin', path: 'admin' },
        tech: { info: [], type: 'Tech', path: 'tech' },
        userStatus: false,
        userEmail: '',
    };
  },
  methods: {
    trackDomen(){WhoisDataService.GetDomain( { Email: this.userEmail } )
          .then(response => {
            if(response.data.Domains.indexOf(this.domainName) == -1)
            WhoisDataService.AddDomain(this.domainName, {Email: this.userEmail, Domains: response.data.Domains}).
            then(res=> {
              console.log(res);
            });
          });
    },
      StatusUserCheck(hideForm, uemail, ustatus){
        this.authForm = hideForm;
        this.userEmail = uemail;
        this.userStatus = ustatus;
        console.log(uemail);
      },
      updateWidth() {
        this.width = window.innerWidth;
      },
      GetHoisInfo() {
        var data = {
          name: this.domainName,
        };
        WhoisDataService.getWhoisInfo(data)
          .then(response => {
            // console.log(response.data);
            const newServer = response.data
            this.domenNameTemp = this.domainName
            this.domenName = ''
            this.all.info = []
            this.status.info = []
            this.date.info = []
            this.registrant.info = []
            this.admin.info = []
            this.tech.info = []
            this.all.info = newServer.split('\n')
            this.all.info.forEach(element => {
              if (element.indexOf('Date') >= 0) { this.date.info.push(element.replace('Date', '')) } 
              else if ( element.indexOf('Registrant') >= 0) { this.registrant.info.push(element.replace('Registrant', '')) } 
              else if ( element.indexOf('Admin') >= 0) { this.admin.info.push(element.replace('Admin', '')) } 
              else if ( element.indexOf('Tech') >= 0) { this.tech.info.push(element.replace('Tech', '')) } 
              else if ( element.indexOf('Status') >= 0 || element.indexOf('state') >= 0) { this.status.info.push(element) } 
              else if ( element.indexOf('No entries found for the selected source(s).') >= 0 ||
                        element.indexOf('No match for domain') >= 0 ||
                        element.indexOf('ERROR') >= 0 ||
                        element.indexOf('NOT FOUND') >= 0 ||
                        element.indexOf('not found') >= 0) {
                          this.all.info = ['NOT FOUND']
                        }
            })
            if (this.all.info.length < 2) {
              this.status.info = []
              this.date.info = []
              this.registrant.info = []
              this.admin.info = []
              this.tech.info = []
            }
            // console.log(typeof newServer)
          })
          .catch(e => {
            console.log(e);
          });
    },
  },
  created() {
    window.addEventListener("resize", this.updateWidth);
  },
}
</script>

