<template>
  <div class="AuthFormMain">
    <table style="width: 90%; margin-left: 40px;">
        <td style="width: 1vw;">
            <p class="LogAndSign" v-bind:class="{ LogOrSign: isLog}" @click="isLog = true">Log In</p>
        </td>
        <td style="width: 10vw;">
            <p class="LogAndSign" v-bind:class="{ LogOrSign: !isLog}" @click="isLog = false">Sign Up</p>
        </td>
        <td style="width: 20%;">
            <div @click="$emit('authorizationClick', false, '', false)" class="cl-btn-7"></div>
        </td>
    </table>

    <div class='AuthFormInput'>
        <input v-model="Name" type='text' placeholder='Name' class='input-line' v-show="!isLog"/>
        <input v-model="Email" type='email' placeholder='E-mail' class='input-line'/>
        <input v-model="Password" type='password' placeholder='Password' class='input-line'/>
        <input v-model="RePassword" type='password' placeholder='Repeat Password' class='input-line' v-show="!isLog"/>
        <button class="LogBtn" v-show="isLog" @click="LogIn">Log In</button>
        <button class="LogBtn" v-show="!isLog" @click="SignUp">Sign Up</button>
    </div>

  </div>
</template>

<script>
import WhoisDataService from '../services/WhoisDataService';

export default {
  name: 'AuthForm',
  data() {
    return{
        isLog: true,
        Name: '',
        Email: '',
        Password: '',
        RePassword: '',
    }
  },
  methods: {
    SignUp(){
        if(this.Password == this.RePassword){
            WhoisDataService.FindByEmail(this.Email).then(response => {
                console.log(response.data.Name);
                if(response.data.Name) {
                    console.log("Почта занята");
                    return;
                }
                else{
                    WhoisDataService.create({
                        Name: this.Name,
                        Email: this.Email,
                        Password: this.Password,
                        Domains: ''
                    }).then(response => {
                        console.log(response);
                    });
                }
            });
        }
        else console.log("НЕ СОВПАДАЕТ");
    },
    LogIn(){
        WhoisDataService.GetPassword(this.Email, { Password: this.Password }).then(response => {
            if(response.data.status){
                console.log(response.data.status);
                this.$emit('authorizationClick', false, this.Email, true);
            }
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.AuthFormMain{
    opacity: 0.93;
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
    height: 100%;
    width: 50%;
    background: #FFF;
    box-shadow: -14px 0px 24px -12px #000000;
}

.cl-btn-7 {
    width: 40px;
    height: 40px;
    border-radius: 40px;
    position: relative;
    z-index: 1;
    margin: 20px auto;
    cursor: pointer;
    right: -30%;
}
.cl-btn-7:before {
    content: '+';
    color: #333;
    position: absolute;
    z-index: 2;
    transform: rotate(45deg);
    font-size: 50px;
    line-height: 1;
    top: -5px;
    left: 6px;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
}
.cl-btn-7:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: #333;
    z-index: 1;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
    transform: scale(0.01);
}
.cl-btn-7:hover:after {
    transform: scale(1);
}
.cl-btn-7:hover:before {
    transform: scale(0.8) rotate(45deg);
    color: #fff;
}

.LogOrSign{
    color: #555;
    text-decoration: underline #555;
    text-underline-offset: 20px;
}
.LogAndSign{
    font-size: calc(2em + 0.5vw);
}
.LogAndSign:hover {
    color: #999;
}
.LogOrSign:hover{
    color: #999;
    text-decoration: underline #999;
}

.AuthFormInput{
    margin-left: 7vw;
    top: 15vh;
    position: absolute;
}
.input-line{
    padding-left: 20px;
    border-radius: 30px;
    width: 70%;
    margin-bottom: 30px;
    height: calc(1em + 1vw);
    font-size: calc(0.6em + 1vw);
}

.LogBtn{
    background: #333;
    color: #CCC;
    border: none;
    border-radius: 30px;
    width: calc(70% + 1.5vw);
    height: calc(1em + 1vw);
    font-size: calc(0.7em + 1vw);
}
.LogBtn:hover{
    opacity: 0.6;
    background: #BBB;
    color: #222;
}

.LogBtn:active{
    opacity: 0.6;
    background: #EEE;
    color: #555;
}

@media only screen and (max-width: 800px)  {
  .LogAndSign{
    font-size: calc(1em + 1.5vw);
  }
  .AuthFormInput{
    margin-left: 7vw;
    top: 10vh;
    position: absolute;
  }
}

</style>
